import uniIndexedList from "@/components/uni-indexed-list/uni-indexed-list.vue"
export default {
	data() {
		return {
			cityName:"莆田市",
			list: [{
				"letter": "p",
				"data": [
					"莆田市"
				]
			}, {
				"letter": "g",
				"data": [
					"广州市"
				]
			}]
		}
	},
	components: {
		uniIndexedList,
	},
	methods: {
		// 选择城市，跳转并传参
		bindClick(e){
			this.cityName = e.item.name;
			uni.navigateTo({
			    url: `selectAddress?cityName=${this.cityName}`
			});
		}
	}
}
